"use scrict";
import { HOME } from "@/config/router.js";
import { logo } from "@/config/image.js";
import { checkUserInfo } from "@/config/comm.js";
import { mapActions } from "vuex";

export default {
	data() {
		return {
			logo,
			source: "",
			index: 1,
			login: {
				username: "",
				pwd: ""
			},
			detail: {
				mobile: "",
				code: ""
			}
		}
	},
	computed: {
		sendCodeDisabled() {
			let infor = this.detail;
			return !infor.mobile || infor.mobile.length !== 11;
		},
		detailDisabled() {
			let infor = this.detail;
			return !infor.mobile || infor.mobile.length !== 11 || !infor.code || infor.code.length !== 5;
		}
	},
	onLoad(options) {
		this.source = options.source || HOME;
	},
	methods: {
		...mapActions("user", ["loadUserInfo"]),
		onLogin: async function () {
			await this.$store.dispatch("user/login", this.login);
			this.$toPage(this.source);
		},
		sendCode: async function () {
			let res = await this.$post(this.$api.SEND_VERIFICATION_CODE, {
				mobile: this.detail.mobile,
				type: "REG"
			});
			this.$toast({
				title: res.msg,
				icon: "success"
			});
		},
		ondetail: async function () {
			await this.$post(this.$api.BIND_PHONE, this.detail);
			this.$toast({
				title: "手机绑定成功",
				icon: "success"
			});
			await this.loadUserInfo();
			checkUserInfo();
		}
	}
}
